<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i>会员级别
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
            >
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="Level" label="级别" width="55" align="center"></el-table-column>
                <el-table-column prop="AwardRatio" label="奖励比例">
                    <template slot-scope="scope">{{scope.row.AwardRatio*100|money3}}%</template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button
                                type="text"
                                icon="el-icon-edit"
                                @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="400px">
            <el-form ref="form" :model="form" label-width="120px">
                <el-form-item label="级别">
                    <el-input readonly v-model="form.Level" ></el-input>
                </el-form-item>
                <el-form-item label="奖励比例">
                    <el-input-number class="loose-input" :precision="4"  v-model="form.AwardRatio"   :step="0.001" :min="0" ></el-input-number>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
    
</template>

<script>
    import {request} from '../../utils/http';
    export default {
        name: 'LevelAward',
        data() {
            return {
                AwardTypeItem:[{name:'金币',value:1},{name:'积分',value:2}],
                query: {
                    address: '',
                    name: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                tableData: [],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1
            };
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                request({ url:'filmbll/getyhlevelawards',  method:'get' }).then((res)=>{
                    console.log(res);
                    if (res.code==200){
                        this.tableData = res.obj;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.getData();
            },
            // 编辑操作
            handleEdit(index, row) {
                this.idx = index;
                this.form = row;
                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                request({
                    url:'filmbll/updateyhlevelaward', method: 'post',
                    data:this.form
                }).then((res)=>{
                    console.log(res);
                    if (res.code==200){
                        this.editVisible = false;
                        this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                        this.$set(this.tableData, this.idx, this.form);
                    } else {
                        this.$message.error('更新失败：'+res.msg);
                    }
                });
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
</style>
